import React from 'react'

export default function Jumbotron(props) {
  return (
    <div  className='container-fluid jumbotron'>
        <div className='row'>
            <div className='col text-center p-5'>
                <h1>{props.title}</h1>
                <p className='lead'>{props.subTitle}</p>

            </div>

        </div>

    </div>
  )
}
