import React, {useState, useEffect} from 'react'
import { useAuth } from '../../context/auth'
import Jumbotron from '../../components/cards/Jumbotron'
import AdminMenu from '../../components/nav/AdminMenu'
import Axios from 'axios'
import toast from "react-hot-toast"
import { useNavigate, useParams } from 'react-router-dom'

import { Select } from 'antd';
import { Option } from 'rc-select';


export default function Product() {

  const [auth, setAuth] = useAuth();
  //list categories
  const [categories, setCategories] = useState([]);

  const navigate = useNavigate();
  const params = useParams();

  //Product Fields
  const [name, setname] = useState('');
  const [description, setdescription] = useState('');
  const [price, setprice] = useState('');
  const [category, setcategory] = useState('');
  const [quantity, setquantity] = useState('');
  const [shipping, setshipping] = useState('');
  const [photo, setphoto] = useState('');
  const [id, setId] = useState('')

  useEffect(() => {
    LoadCategories();
    LoadProduct();
  }, []);

  const LoadCategories = async() => {
    try {
      const {data} = await Axios.get("/categories");
      if(data?.error){
        toast.error(data?.error);
      } else {
        setCategories(data);
      }
      setCategories(data)
    } catch (err) {
      console.log(err)
    }
  }

  
  const LoadProduct = async() => {
    try {
      const {data} = await Axios.get(`/product/${params.slug}`);
      if (data?.error){
        toast.error(data?.error)
      } else {

        setId(data._id);
        setname(data.name);
        setdescription(data.description);
        setprice(data.price);
        setcategory(data.category._id);
        setquantity(data.quantity);
        setshipping(data.shipping);
      }
    } catch (err) {
      console.log(err)
    }
  }

  const handleSubmit = async(e) => {
    e.preventDefault();
    try {
      const productData = new FormData();
      productData.append("name", name);
      productData.append("description", description);
      productData.append("price", price);
      productData.append("category", category);
      productData.append("quantity", quantity);
      productData.append("shipping", shipping);
      photo && productData.append("photo", photo);

      const {data} = await Axios.put(`/product/${id}`,productData);
      if(data?.error) {
        toast.error(data?.error)
      } else {
        toast.success(`Product ${name} Upated`)
      }

      navigate("/dashboard/admin/products");
    } catch (err) {
      toast.error("Product Update Failed Try Again!")
    }
  }

  const handleDelete = async() => {
    try {
        const answer = window.confirm("Are You sure want to delete this product")
        if (!answer) return
        const {data} = await Axios.delete(`/product/${id}`);
        if(data?.error){
            toast.error(data?.error)
        } else {
            toast.success(`The Product ${name} has deleted`);
            navigate("/dashboard/admin/products")
        }
    } catch (err) {
        toast.error("Error with deleting this product try again later")
    }

  }
  return (
    <>
      <Jumbotron 
        title = {`Welcome ${auth?.user?.name}`}
        subTitle = "Admin Dashboard"
      />
      <div className='contianer-fluid'>
        <div className='row'>
          <div className='col-md-3'>
            <AdminMenu/>
          </div>
            <div className='col-md-9'>
              <div className='p-3 mt-2 mb-2 h4 bg-light'>Create product</div>
              {/* Preview Photo */}
              {photo ?
                (
                  <div className='text-center'>
                    <img 
                      src={URL.createObjectURL(photo)}
                      alt={photo.name}
                      className= 'img img-responsive'
                      height="200px"
                    />

                  </div>
                ) :
                (
                  <div className='text-center'>
                    <img 
                      src={`${process.env.REACT_APP_API}/product/photo/${id}?${new Date().getTime()}`}
                      alt="Product photo"
                      className= 'img img-responsive'
                      height="200px"
                    />

                  </div>
                )
              }
              {/* Form Fields */}
              <div className='pt-2'>
                <label className='btn btn-outline-secondary p-2 col-12 mb-3'>
                  {photo ? photo.name : "Upload Photo"}
                  <input
                    type="file"
                    name= "photo"
                    accept='image/*'
                    onChange={(e) => setphoto(e.target.files[0])}
                    hidden
                  />
                </label>
              </div>

              <input
                type="text"
                className='form-control p-2 mb-3'
                placeholder='Enter Name'
                value={name}
                onChange={(e) => setname(e.target.value)}
              />

              <textarea
                type="text"
                className='form-control p-2 mb-3'
                placeholder='Enter Description'
                value={description}
                onChange={(e) => setdescription(e.target.value)}
              />

              <input
                type="Number"
                className='form-control p-2 mb-3'
                placeholder='Enter Price'
                value={price}
                onChange={(e) => setprice(e.target.value)}
              />

              <Select
                // showSearch
                bordered={false}
                side="large"
                className='form-select mb-3'
                placeholder="Choose Category"
                onChange={(value) => setcategory(value)}
                value = {category}
              >
                {categories?.map((item) => (
                  <Option key={item._id} value={item._id}>
                    {item.name}
                  </Option>
                )
                )}
              </Select>

              <input
                type="Number"
                min="1"
                className='form-control p-2 mb-3'
                placeholder='Enter Quantity'
                value={quantity}
                onChange={(e) => setquantity(e.target.value)}
              />

              <Select
                showSearch
                bordered={false}
                side="large"
                className='form-select mb-3'
                placeholder="Available Shipping"
                onChange={(value) => setshipping(value)}
                value = {shipping ? "Yes" : "No"}
              >
              <Option value="1">YES</Option>
              <Option value="0">No</Option>
              </Select>
              <div className='d-flex justify-content-between'>
                <button onClick={handleSubmit} className='btn btn-primary mb-5'>SUBMIT</button>
                <button onClick={handleDelete} className='btn btn-danger mb-5'>DELETE</button>
              </div>
              
            </div>
        </div>

      </div>
    </>
  )
}
