import React, {useEffect, useState} from 'react'
import Axios from "axios"
import toast from 'react-hot-toast'
import { Checkbox, Radio } from 'antd'

import Jumbotron from "../components/cards/Jumbotron"
import ProductCard from "../components/cards/ProductCard"
import {Prices} from '../Prices';

export default function Shop() {
    const [products, setProducts] = useState([]);
    const [categories, setCategories] = useState([]);
    const [checked, setChecked] = useState([]); //Categories
    const [radio, setradio] = useState([]); //Prices

    useEffect(() => {
        LoadCategories();
      }, []);

      useEffect(() => {
        if(!checked.length || !radio.length) LoadProducts();
      }, []);

      useEffect(() => {
        if(checked.length || radio.length) loadFilterProducts();
      }, [checked, radio]);

      const LoadCategories = async() => {
        try {
          const {data} = await Axios.get("/api/categories");
          if(data?.error){
            toast.error(data?.error);
          } else {
            setCategories(data);
          }
          setCategories(data)
        } catch (err) {
          console.log(err)
        }
      } 
      
      const LoadProducts = async() => {
        try {
          const {data} = await Axios.get(`api/products`);
          if (data?.error){
            toast.error(data?.error)
          } else {
            setProducts(data)
          }
        } catch (err) {
          console.log(err)
        }
      }

      const handleCheck = (value, id) => {
        let all = [...checked];
        if (value) {
            all.push(id);
        } else {
            all = all.filter((c) => c !==id);
        }
        setChecked(all);
        
      }

      const loadFilterProducts = async() => {
        try {
          const {data} = await Axios.post(`/filter-products`, {checked, radio})
          if (data?.error){
            toast.error(data?.error)
          } else {
            setProducts(data);
          }
        } catch (error) {
          toast.error("There is a problem with filtering products")
        }
      }


  return (
    <>
        <Jumbotron
            title = "Welcome to Ecommerce Shop Page"
            subTitle = "Check Out all Products"
        />
        {/* <pre>{JSON.stringify({checked, radio}, null, 4)}</pre> */}
        <div className='row'>
        {/* SIDEBAR */}
            <div className='col-md-3'>
                <h3 className='p-3 mt-2 mb-2 h4 bg-light text-center'>Filter By Categories</h3>
                <div className='row p-5'>
                  {/* {categories?.map((c) =>
                      <div style={{marginLeft: "8px"}}>
                          <Checkbox key={c._id} onChange={(e) => handleCheck(e.target.checked, c._id)}>
                              {c.name}
                          </Checkbox>
                      </div>
                  )} */}
                </div>

                <h3 className='p-3 mt-2 mb-2 h4 bg-light text-center'>Filter By Price</h3>
                <div className='row p-5'>
                  <Radio.Group onChange={(e) => setradio(e.target.value)}>
                      {Prices?.map((p) =>
                          <div key={p?._id} style={{marginLeft: "8px"}}>
                              <Radio value={p?.array}>{p?.name}</Radio>
                          </div>
                      )}
                  </Radio.Group>
                </div>
                
                <div className="p-5 pt-0">
                      <button className='btn btn-outline-primary col-12' onClick={() => window.location.reload()}>Reset</button>

                </div>
            </div>
            {/* MAIN CONTENT */}
            <div className='col-md-9'>
                <h2 className='p-3 mt-2 mb-3 text-center bg-light'>{products.length} Products</h2>
                <ProductCard
                    list = {products}
                    colomns = '4'
                    style = {{height: "80vh", overflow: "scroll"}}
                />
            </div>

        </div>
    </>
  )
}
