import React, {useState, useEffect} from 'react'
import { useAuth } from '../../context/auth'
import Jumbotron from '../../components/cards/Jumbotron'
import AdminMenu from '../../components/nav/AdminMenu'
import Axios from 'axios';
import toast from "react-hot-toast"
import { Link } from 'react-router-dom';
import moment from 'moment';

export default function AdminProducts() {

    const [products, setProducts] = useState([]);
    useEffect(() => {
        LoadProducts();
    }, [])

    const LoadProducts = async() => {
        try {
            const {data} = await Axios.get(`/products`);
            if(data?.error) {
                toast.error(data?.error);
            } else {
                setProducts(data);
            }
        } catch (err) {
            console.log(err)
            toast.error("Products Load Failed Refesh the Page");
        }
    }

    

  const [auth, setAuth] = useAuth();
  return (
    <>
      <Jumbotron 
        title = {`Welcome ${auth?.user?.name}`}
        subTitle = "Admin Dashboard"
      />
      <div className='contianer-fluid'>
        <div className='row'>
          <div className='col-md-3'>
            <AdminMenu/>
          </div>
          <div className='col-md-9'>
            <div className='p-3 mt-2 mb-2 h4 bg-light'>Products List ({products.length})</div>
                {products?.map((item) =>
                    <Link
                        key={item._id}
                        to={`/dashboard/admin/product/update/${item.slug}`}
                    >
                    <div className='card mb-3'>
                        <div className='row g-0'>
                            <div className='col-md-4'>
                                <img
                                    src={`${process.env.REACT_APP_API}/product/photo/${item._id}`}
                                    alt={item.name}
                                    className= 'img img-fluid rounded-start'
                                    style={{height: "200px", objectFir:"cover"}}
                                />
                            </div>
                            <div className='col-md-8'>
                                <div className='card-body'>
                                    <h4 className='card-title'>{item.name}</h4>
                                    <p className='card-text'>{item.description}</p>
                                    <p className='card-text'>
                                        <small className='text-muted'>
                                            {moment(item.createdAt).format("MMMM Do YYYY HH:MM A")}
                                        </small>
                                    </p>
                                </div>
                            </div>
                        </div>
                    </div>
                    </Link>

                )}
          </div>
        </div>
      </div>
    </>
  )
}
