import React from 'react'
import moment from 'moment';
import {Badge} from "antd"

export default function ProductCard(props) {
    const list = props.list;
    const colomns = 'col-md-'+ (props.colomns ? props.colomns : 6) ;
  return (
    <div>
        <h3 className='p-3 mt-2 mb-2 h4 bg-light text-center'>{props.title}</h3>
        <div className='row' style={props.style}>
            {list?.map((item) =>
                <div className={colomns} key={item._id}>
                    <div className='card mb-3 hoverble' style={{height: "400px"}}>
                        <Badge.Ribbon text={`${item?.sold} Sold`} color="red">
                            <Badge.Ribbon text={(item?.quantity >= 1 ? `${item.quantity - item.sold} in Stock` : "Out of Stock")} color="green" placement='start'>
                                <img
                                    src={`${process.env.REACT_APP_API}/product/photo/${item._id}`}
                                    alt='Product Image'
                                    style={{height: "200px", objectFir:"cover"}}
                                />
                            </Badge.Ribbon>
                        </Badge.Ribbon>
                        <div className='card-body'>
                            <h5>{item?.name}</h5>

                            <h4 className='fw-bold'>
                                {item?.price?.toLocaleString("en-us", {
                                    style: "currency",
                                    currency: "USD",
                                })}
                            </h4>

                            <p className='card-text'>{item?.description?.substring(0,50)}...</p>
                        </div>
                        <div className='d-flex justify-content-between'>
                            <button className='btn btn-primary col card-button' style={{borderBottomLeftRadius: "5px"}}>View Product</button>
                            <button className='btn btn-primary col card-button' style={{borderBottomRightRadius: "5px"}}>Add to Cart</button>
                        </div>
                    </div>

                    {/* <p>{moment(item.createdAt).fromNow()}</p>
                    <p>{item.sold} Sold</p> */}
                </div>
            )}
        </div>

    </div>
  )
}
