import React, {useState, useEffect} from 'react'
import { useAuth } from '../../context/auth'
import Jumbotron from '../../components/cards/Jumbotron'
import AdminMenu from '../../components/nav/AdminMenu'
import Axios from 'axios';
import toast from 'react-hot-toast';
import CategoryForm from '../../components/forms/CategoryForm';
import { Button, Modal } from 'antd';

export default function Category() {

  const [auth, setAuth] = useAuth();
  
  const [name, setName] = useState("");
  const [category, setCategory] = useState([]);
  const [visible, setVisible] = useState(false);
  const [selected, setSelected] = useState(null);
  const [UpdateName, setUpdateName] = useState();

  useEffect(() => {
    LoadCategories();
  }, []);

  const LoadCategories = async() => {
    try {
      const {data} = await Axios.get("/categories");
      setCategory(data);
    } catch (err) {
      console.log(err);
    }
  }
  

  const handleSubmit = async(e) => {
    e.preventDefault();
    try {
      const {data} = await Axios.post(`/category`, {name});
      if(data?.error){
        toast.error(data.error)
      } else {
        toast.success(`${data?.name} Successfully Created`)
        LoadCategories();
        setName('');
      }
    } catch (err) {
      console.log(err)
      toast.error("Create Category Failed. Try Aain.")
    }
  }

  const handleUpdate = async(e) => {
    e.preventDefault();
    try {
      const {data} = await Axios.put(`/category/${selected._id}`, {
        name: UpdateName
      });
      if(data?.error){
        toast.error(data.error)
      } else {
        toast.success(`Category ${UpdateName} updated Sucessfully`)
        setSelected(null)
        setUpdateName("")
        setVisible(false)
        LoadCategories()
      }
    } catch (err) {
      toast.error("Category Update Failed Try Again")
    }
  }

  const handleDelete = async(e) => {
    e.preventDefault();
    try {
      const {data} = await Axios.delete(`/category/${selected._id}`)
      if (data?.error){
        toast.error(data.error);
      } else {
        toast.success(`The item ${data?.name} Deleted Successfully`)
        setSelected(null)
        setUpdateName('')
        setVisible(false)
        LoadCategories()
      }
    } catch (err) {
      toast.error("Someting went wrong item can not delete")
    }
  }

  return (
    <>
      <Jumbotron 
        title = {`Welcome ${auth?.user?.name}`}
        subTitle = "Admin Dashboard"
      />
      <div className='contianer-fluid'>
        <div className='row'>
          <div className='col-md-3'>
            <AdminMenu/>
          </div>

          <div className='col-md-9'>
            <div className='p-3 mt-2 mb-2 h4 bg-light'>Manage Category</div>
            <CategoryForm
              handleSubmit = {handleSubmit}
              value = {name}
              setValue = {setName}
            />
            <hr/>

            <div className='col'>
              {category?.map((c) => (
                <button 
                  className='btn btn-outline-primary m-3' 
                  key={c._id} 
                  onClick={()=> {
                    setVisible(true)
                    setSelected(c);
                    setUpdateName(c.name)
                    }}>
                    {c.name}</button>))}
            </div>

            <Modal title="Category Update" 
              open={visible} 
              onCancel={()=> setVisible(false)}
              footer={null}
              >
                <CategoryForm
                  handleSubmit = {handleUpdate}
                  value = {UpdateName}
                  setValue = {setUpdateName}
                  btntxt = "UPDATE"
                  handleDelete = {handleDelete}
                />
            </Modal>
          </div>
        </div>
      </div>
    </>
  )
}
